import Vue from 'vue';
import tab from '@/components/tab';
import fixBottom from '@/components/fixBottom';
export default {
    components: {
        tab,
        fixBottom
    },
    data() {
        return {
            tabId: 1,
            showMore: false,
        }
    },
    methods: {
        change: function(e) {
            this.tabId = e;
        },
        /**
         * 是否显示二级菜单
         */
        showList() {
            this.showMore = !this.showMore;
        },

    },
}


