import Vue from 'vue';
export default {
    data() {
        return {
            check: 1,
        }
    },
    props: {
        titleFir: '',
        titleSec: ''
    },
    methods: {
        tabChange: function(check) {
            console.log(document.querySelector('.tab-bar').offsetWidth);
            console.log(document.querySelector('.tab-bar').offsetLeft);
            this.check = check;
            this.$emit('myEvent', check);
            document.getElementsByTagName('p').style.setAttribute('left', 'document.querySelector(".tabBar ").offsetLeft');
        },

    },
    created() {

    }
}


