import Vue from 'vue';
import tab from '@/components/tab';
import fixBottom from '@/components/fixBottom';
export default {
    components: {
        tab,
        fixBottom
    },
    data() {
        return {
            tabId: 1
        }
    },
    methods: {
        change: function(e) {
            this.tabId = e;
        }
    },
}


